.links {
    display: inline-block;
    padding: 20px 0;
    margin: 0;
    transform: translate(-229px, 0px);
    transition-duration: 700ms;
}

.links:hover {
    transform: translate(0px, 0px);
}

.links li{
    display: inline;
    list-style-type: none;
    padding: 0 10px;
}

.link {
    text-decoration: none;
    font-weight: bold;
    color: #ceb8b8;
}

.active {
    text-decoration: none;
    font-weight: bold;
    color: #f59898;
}

.square {
    background: black;
    border-radius: 3px;
    padding: 0 10px;
}

.bgc {
    background-color: #126b7b;
}
